// import 文を使って sub.js ファイルを読み込む。
import { hello } from "./sub";
import { backfaceFixed } from "./utils/backfaceFixed";

// sub.jsに定義されたJavaScriptを実行する。
hello();

const open = () => {
  // 背面コンテンツのスクロールを無効にする
  backfaceFixed(true);
};

const close = () => {
  // 背面コンテンツのスクロールの無効を解除する
  backfaceFixed(false);
};
